html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-family: Roboto, Helvetica, Arial, sans-serif;
  font-size: 16px;
}

.h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.container {
  display: flex;
  flex-direction: column;
  max-width: 700px;
  height: 100vh;
  margin: 0 auto;
}

.content {
  flex: 1 1 auto;
  overflow: auto;
  min-height: 0;
}

.block {
  display: flex;
}

.input {
  width: 100%;
  height: 40px;
  line-height: 40px;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #afafaf;
  font-size: 20px;
}

.input:focus {
  box-shadow: 0 0 5px #51cbee;
}

.btn {
  height: 40px;
  display: inline-flex;
  padding: 0 20px;
  background: #09b02e;
  color: #fff;
  border-radius: 4px;
  font-size: 20px;
  transition: background .3s;
}

.btn:hover {
  background: #089828;
}

.form-start {
  margin-bottom: 20px;
}

.form-start__field {
  background: #f6f6f6;
  padding: 10px;
  display: flex;
}

.form-start__input {
  flex: 1 0 auto;
  margin-right: 10px;
}

.forms {
  background: #f6f6f6;
}

.form {
  padding: 10px;
}

.form-group {
  font-size: 0;
  margin: 0 -10px -10px;
}

.form-item {
  display: inline-block;
  margin: 0 10px 10px;
  font-size: 14px;
}

.repo {
  display: block;
  padding: 10px 0;
  color: #000;
  text-decoration: none;
}

.repo__name {
  font-weight: 500;
  font-size: 20px;
  margin-bottom: 8px;
}

.repo__forked {
  font-size: 12px;
  color: #bebebe;
  margin-bottom: 10px;
}

.repo__description {
  margin-bottom: 8px;
}

.repo__footer {
  display: flex;
  font-size: 14px;
}

.repo__stars, .repo__lang {
  margin-right: 10px;
}

.list {
  padding: 0 10px;
  margin-bottom: 20px;
}

.list__item {
  border-bottom: 1px solid #afafaf;
}

.pagination {
  padding: 10px;
  text-align: center;
}
